import { LOCK_ICON } from '../components/LockIcon';

const PREMIUM_URL = process.env.PREMIUM_URL || '#';

const RESTRICTION_LEVEL = Number(
  process.env.YOUTUBE_VIDEO_QUALITY_RESTRICTION_LEVEL || 1080,
);

export function createOverlayLink(href: string): string {
  return `
    <a
    class="ytp-vpnn-overlay-link"
      href="${href}"
      target="_blank"
      rel="noopener noreferrer"
    ></a>
  `;
}

export function getQualityFromText(text: string): number {
  const normalized = text.trim().toLowerCase();

  if (
    normalized === 'auto' ||
    normalized === 'авто' ||
    normalized.includes('автонастройка')
  ) {
    return -1;
  }

  const match = normalized.match(/(\d{3,4})p/);
  return match ? Number(match[1]) : 0;
}

export function updateQualityMenuItem(
  menuItem: HTMLElement,
  configsBaseUrl?: string,
): void {
  if (menuItem.hasAttribute('data-vpnn-locked')) return;

  const premiumUrl = configsBaseUrl
    ? `${configsBaseUrl}${PREMIUM_URL}`
    : PREMIUM_URL;

  menuItem.setAttribute('data-vpnn-locked', 'true');

  // Добавляем иконку замка
  const childTarget = menuItem.firstElementChild as HTMLElement | null;

  if (childTarget && !childTarget.querySelector('.ytp-vpnn-lock')) {
    const lock = document.createElement('span');
    lock.className = 'ytp-vpnn-lock-icon';
    lock.innerHTML = LOCK_ICON;

    childTarget.prepend(lock);
    childTarget.classList.add('ytp-vpnn-item');
  }

  // Добавляем ссылку на рекламу премиум
  if (!menuItem.querySelector('.ytp-vpnn-overlay-link')) {
    const wrapper = document.createElement('div');
    wrapper.innerHTML = createOverlayLink(premiumUrl);
    const linkEl = wrapper.firstElementChild!;
    linkEl.addEventListener('click', (e) => e.stopPropagation());
    menuItem.appendChild(linkEl);
  }
}

export function hideQualityMenuItem(menuItem: HTMLElement): void {
  if (!menuItem.classList.contains('ytp-vpnn-hidden')) {
    menuItem.classList.add('ytp-vpnn-hidden');
  }
}

export function revertUpdateQualityMenuItem(menuItem: HTMLElement): void {
  // Remove lock attribute & hiding class
  menuItem.removeAttribute('data-vpnn-locked');
  menuItem.classList.remove('ytp-vpnn-hidden');

  // Remove lock icon and class from child
  const childTarget = menuItem.firstElementChild as HTMLElement | null;
  if (childTarget) {
    const lockIcon = childTarget.querySelector('.ytp-vpnn-lock-icon');
    if (lockIcon) lockIcon.remove();
    childTarget.classList.remove('ytp-vpnn-item');
  }

  // Remove overlay link
  const overlayLink = menuItem.querySelector('.ytp-vpnn-overlay-link');
  if (overlayLink) overlayLink.remove();
}

export function updateAllQualityMenuItems(configsBaseUrl?: string): void {
  let qualityMenuItems = document.querySelectorAll<HTMLElement>(
    '[role="menuitemradio"]',
  );

  if (qualityMenuItems.length > 0) {
    qualityMenuItems.forEach((item) => {
      const quality = getQualityFromText(item.textContent || '');

      if (quality === -1) {
        hideQualityMenuItem(item);
        return;
      }

      if (!quality || quality <= RESTRICTION_LEVEL) return;

      updateQualityMenuItem(item, configsBaseUrl);
    });
  }
}

export function revertUpdateAllQualityMenuItems(): void {
  let qualityMenuItems = document.querySelectorAll<HTMLElement>(
    '[role="menuitemradio"]',
  );

  if (qualityMenuItems.length === 0) {
    return;
  }

  qualityMenuItems.forEach((item) => revertUpdateQualityMenuItem(item));
}
