import { Actions } from '@app/actions/shared';
import {
  updateAllQualityMenuItems,
  revertUpdateAllQualityMenuItems,
} from './utils/qualityMenuUtils';
import { YTB_SCRIPT_SOURCE, YTbPlayerActions } from './types/player';
import { isMessageOfType } from '@app/messages/shared';
import { browser_api } from '@app/browser_api';
import {
  ConnectionState,
  PaymentStatus,
} from '@app/storage/local/types/persistentTypes';

if (
  window.location.host.includes('youtube.com') ||
  window.location.host.includes('youtu.be')
) {
  console.warn('[Ytb-quality-restriction] Observer initialized');

  let isRestrictionEnabled: boolean = false;
  let configsBaseUrl: string | undefined;
  let status: PaymentStatus | undefined;
  let connectionState: ConnectionState | undefined;
  let observer: MutationObserver | null = null;

  function observerCallback() {
    if (isRestrictionEnabled) {
      window.postMessage(
        {
          source: YTB_SCRIPT_SOURCE,
          type: YTbPlayerActions['set-quality'],
        },
        '*',
      );
      updateAllQualityMenuItems(configsBaseUrl);
    } else {
      revertUpdateAllQualityMenuItems();
      window.postMessage(
        {
          source: YTB_SCRIPT_SOURCE,
          type: YTbPlayerActions['stop-restrict-quality'],
        },
        '*',
      );
    }
  }

  function setRestrictionByStatusAndConnection() {
    isRestrictionEnabled = status === 'Free' && connectionState === 'connected';
    observerCallback();
  }

  if (!observer) {
    observer = new MutationObserver(observerCallback);
    observer.observe(document.body, {
      childList: true,
      subtree: true,
    });
    console.warn('[Ytb-quality-restriction] MutationObserver initialized');
  }

  browser_api.runtime.onMessage.addListener((message: unknown) => {
    if (isMessageOfType(message, Actions.ConfigsBaseUrl)) {
      configsBaseUrl = message.payload.configsBaseUrl;
    }

    if (isMessageOfType(message, Actions.UserStatusChanged)) {
      status = message.payload.status;
      setRestrictionByStatusAndConnection();
    }

    if (isMessageOfType(message, Actions.ConnectionStateChanged)) {
      connectionState = message.payload.connectionState;
      setRestrictionByStatusAndConnection();
    }
  });
} else {
  console.warn('[Ytb-quality-restriction] Not a YouTube tab, script exiting');
}
